/**
 * Customer Service - API calls for customer management
 */

const API_BASE = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

class CustomerService {
  async getAuthHeaders(noCache = false) {
    const token = localStorage.getItem('token');
    
    // Debug th√¥ng tin auth
    if (!token) {
      console.warn('‚ö†Ô∏è No token found in localStorage');
    } else {
      console.log('‚úÖ Token found, length:', token.length);
    }
    
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`
    };
    
    if (noCache) {
      headers['Cache-Control'] = 'no-cache';
      headers['Pragma'] = 'no-cache';
    }
    
    return headers;
  }

  async handleResponse(response) {
    const data = await response.json();
    
    if (!response.ok) {
      // Debug th√¥ng tin chi ti·∫øt v·ªÅ l·ªói
      console.error('‚ùå CustomerService API Error:');
      console.error('  Status:', response.status);
      console.error('  Status Text:', response.statusText);
      console.error('  Response Data:', data);
      
      // Ki·ªÉm tra l·ªói validation c·ª• th·ªÉ
      if (response.status === 400 && data.errors) {
        console.error('  Validation Errors:');
        data.errors.forEach(err => {
          console.error(`    - ${err.path || err.param}: ${err.msg}`);
        });
      }
      
      throw new Error(data.message || `HTTP error! status: ${response.status}`);
    }
    
    return data;
  }

  // L·∫•y danh s√°ch kh√°ch h√†ng
  async getCustomers(params = {}) {
    const queryString = new URLSearchParams(params).toString();
    const url = `${API_BASE}/customers${queryString ? `?${queryString}` : ''}`;
    
    const response = await fetch(url, {
      method: 'GET',
      headers: await this.getAuthHeaders(true) // Force no-cache for fresh data
    });
    
    return this.handleResponse(response);
  }

  // L·∫•y chi ti·∫øt kh√°ch h√†ng
  async getCustomer(id) {
    const response = await fetch(`${API_BASE}/customers/${id}`, {
      method: 'GET',
      headers: await this.getAuthHeaders()
    });
    
    return this.handleResponse(response);
  }

  // T·∫°o kh√°ch h√†ng m·ªõi
  async createCustomer(customerData) {
    const response = await fetch(`${API_BASE}/customers`, {
      method: 'POST',
      headers: await this.getAuthHeaders(),
      body: JSON.stringify(customerData)
    });
    
    return this.handleResponse(response);
  }

  // C·∫≠p nh·∫≠t kh√°ch h√†ng
  async updateCustomer(id, customerData) {
    const response = await fetch(`${API_BASE}/customers/${id}`, {
      method: 'PUT',
      headers: await this.getAuthHeaders(),
      body: JSON.stringify(customerData)
    });
    
    return this.handleResponse(response);
  }

  // X√≥a kh√°ch h√†ng
  async deleteCustomer(id) {
    const response = await fetch(`${API_BASE}/customers/${id}`, {
      method: 'DELETE',
      headers: await this.getAuthHeaders()
    });
    
    return this.handleResponse(response);
  }

  // L·∫•y h·ª£p ƒë·ªìng c·ªßa kh√°ch h√†ng
  async getCustomerContracts(customerId, status = null) {
    const params = status ? { status } : {};
    const queryString = new URLSearchParams(params).toString();
    const url = `${API_BASE}/customers/${customerId}/contracts${queryString ? `?${queryString}` : ''}`;
    
    const response = await fetch(url, {
      method: 'GET',
      headers: await this.getAuthHeaders()
    });
    
    return this.handleResponse(response);
  }

  // T·∫°o h·ª£p ƒë·ªìng m·ªõi
  async createContract(contractData) {
    const response = await fetch(`${API_BASE}/customers/contracts`, {
      method: 'POST',
      headers: await this.getAuthHeaders(),
      body: JSON.stringify(contractData)
    });
    
    return this.handleResponse(response);
  }

  // L·∫•y h·ª£p ƒë·ªìng s·∫Øp h·∫øt h·∫°n
  async getExpiringContracts(days = 30) {
    const response = await fetch(`${API_BASE}/customers/contracts/expiring?days=${days}`, {
      method: 'GET',
      headers: await this.getAuthHeaders()
    });
    
    return this.handleResponse(response);
  }

  // L·∫•y th·ªëng k√™ kh√°ch h√†ng
  async getCustomerStats() {
    const response = await fetch(`${API_BASE}/customers/stats`, {
      method: 'GET',
      headers: await this.getAuthHeaders()
    });
    
    return this.handleResponse(response);
  }
}

const customerService = new CustomerService();
export default customerService;